/* slider */

.slider {
  background: white;
  height: 300px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  top: 65px;
  width: 90%;
  border-radius: 6px;
}

.slider::before,
.slider::after {
  content: "";
  height: 100%;
  position: absolute;
  width: 100px;
  z-index: 2;
}

.slider::after {
  right: 0;
  top: 0;
  -webkit-transform: rotateZ(180deg);
  transform: rotateZ(180deg);
}

.slider::before {
  left: 0;
  top: 0;
}

.slider .slide-track {
  animation: scroll 80s linear infinite;
  display: flex;
  width: calc(300px * 18);
}

.slider .slide-track:hover {
  animation-play-state: paused;
}

.slider .slide {
  height: 300px;
  width: 300px;
}

.slide:nth-child(1),
.slide:nth-child(10) {
  background: url(./Components/images/gallary/1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-origin: border-box;
}

.slide:nth-child(2),
.slide:nth-child(11) {
  background: url(./Components/images/gallary/2.jpg);
